<template>
  <div class="user-chat-page">
    <van-nav-bar
      fixed
      left-arrow
      @click-left="$router.back()"
      title="小智同学"
    ></van-nav-bar>
    <!-- 聊天列表 -->
        <!-- 聊天列表 -->
    <div class="chat-list" ref="list">
      <div class="chat-item"
        v-for="(item,i) in list"
        :key="i"
        :class="{left:item.name==='xz',right:item.name==='my'}"
      >
        <geek-icon v-if="item.name==='xz'" name="xiaozhi" />
        <div class="chat-pao">{{item.msg}}</div>
        <van-image v-if="item.name==='my'" fit="cover" round :src="myAvatar" />
      </div>
    </div>
    <!-- 输入框 -->
    <div class="reply-container van-hairline--top">
      <van-field
        v-model.trim="value"
        left-icon="edit"
        placeholder="请描述您的问题"
      ></van-field>
      <span class="send" @click="sengMsg">发送</span>
    </div>
  </div>
</template>

<script>
import socketIO from 'socket.io-client'
import { getUserProfile } from '@/api/user'
export default {
  name: 'UserChatPage',
  data () {
    return {
      myAvatar: '',
      list: [],
      value: '',
      io: null
    }
  },
  methods: {
    sengMsg () {
      if (!this.value) return
      this.io.emit('message', {
        msg: this.value,
        timestamp: +new Date()
      })
      // 将这条消息加入到list中去
      this.list.push({
        name: 'my',
        msg: this.value
      })
      this.scrollToBottom()
      // 清空value
      this.value = ''
    },
    scrollToBottom () {
    //   console.log(this.$refs.list.scrollHeight)
    //   console.log(this.$refs.list.offsetHeight)
      // 让list滚动到差值的位置
      this.$nextTick(() => {
        // 当页面渲染完毕再执行以下语句
        this.$refs.list.scrollTop = this.$refs.list.scrollHeight - this.$refs.list.offsetHeight
      })
    }
  },
  async created () {
    const [, res] = await getUserProfile()
    this.myAvatar = res.data.data.photo

    // 连接webscoket协议
    this.io = socketIO('http://101.35.25.8:8001', {
      query: {
        token: this.$store.state.user.token
      },
      transports: ['websocket']
    })
    this.io.on('connect', () => {
      console.log('链接服务器成功')
      this.list.push({
        name: 'xz',
        msg: '小智来了，有什么问题吗'
      })
      this.scrollToBottom()
    })
    this.io.on('message', data => {
      this.list.push({
        name: 'xz',
        msg: data.msg
      })
      this.scrollToBottom()
    })
  }
}
</script>
<style lang="less" scoped>
.user-chat-page {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  box-sizing: border-box;
  background:#fff;
  padding: 46px 0 50px 0;
  .chat-list {
    height: 100%;
    overflow-y: scroll;
    .chat-item{
      padding: 10px;
      .van-image{
        vertical-align: middle;
        width: 40px;
        height: 40px;
      }
      .geek-icon {
        font-size: 40px;
        line-height: 0;
      }
      .chat-pao{
        vertical-align: top;
        display: inline-block;
        min-width: 40px;
        max-width: 70%;
        min-height: 40px;
        line-height: 20px;
        border-radius: 4px;
        position: relative;
        padding: 10px;
        background-color: @geek-gray-color;
        word-break: break-all;
        font-size: 14px;
        color: #333;
        &::before{
          content: "";
          width: 6px;
          height: 6px;
          position: absolute;
          top: 15px;
          background:  @geek-gray-color;
        }
      }
    }
  }
}
.chat-item.right{
  text-align: right;
  .chat-pao{
    margin-left: 0;
    margin-right: 10px;
    &::before{
      right: -3px;
      transform: rotate(45deg);
    }
  }
}
.chat-item.left{
  text-align: left;
  .chat-pao{
    margin-left: 10px;
    margin-right: 0;
    &::before{
      left: -3px;
      transform: rotate(-135deg);
    }
  }
}
.reply-container {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 49px;
  width: 100%;
  background: #fff;
  z-index: 9999;
  display: flex;
  align-items: center;
  padding: 0 10px;
  > .van-field {
    flex: 1;
    background: #F7F8FA;
    height: 32px;
    border-radius: 16px;
    padding: 0 10px;
    line-height: 32px;
    ::v-deep .van-field__left-icon .van-icon {
      color: #ccc;
    }
  }
  > .send {
    margin-left: 10px;
    font-size: 14px;
    color: #999;
  }
}
</style>
